import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { ServicesComponent } from './components/services/services.component';
import {AboutUsComponent} from './components/about-us/about-us.component'
import { TeamComponent } from './components/team/team.component';
import { ClientComponent } from './components/client/client.component';
import { CareersComponent } from './components/careers/careers.component';
import { ContactUsComponent } from './components/contact-us/contact-us.component';
import { AuditComponent } from './components/audit/audit.component';
import { TaxConsultingComponent } from './components/tax-consulting/tax-consulting.component';
import { ManagementConsultingComponent } from './components/management-consulting/management-consulting.component';
import { FinanceConsultingComponent } from './components/finance-consulting/finance-consulting.component';
import { PayrollAccountingComponent } from './components/payroll-accounting/payroll-accounting.component';


const routes: Routes = [
  { path : '', redirectTo:'index', pathMatch:'full'},
  { path : 'index', component: HomeComponent},
  { path: 'index/home', component: HomeComponent },
  { path : 'index/about-us', component: AboutUsComponent},
  { path: 'index/team', component: TeamComponent },
  { path : 'index/services', component: ServicesComponent},
  { path: 'index/client', component: ClientComponent },
  { path : 'index/careers', component: CareersComponent},
  { path: 'index/contact-us', component: ContactUsComponent },

  { path : 'index/services/audit', component: AuditComponent},
  { path: 'index/services/tax-consultancy', component: TaxConsultingComponent },
  { path : 'index/services/management-consultancy', component: ManagementConsultingComponent},
  { path: 'index/services/financial-accounting', component: FinanceConsultingComponent },
  { path : 'index/services/payroll-accounting', component: PayrollAccountingComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
